import axios from '@/http/axios.ts';
// import axios from 'axios';
import qs from 'qs';
// 课时数据列表
function getClassHour(data: any) {
    return axios.get('/a/u/allLesson', {
        params: data,
    });
}
// 课时数据列表
function getClassHourFuzzy(data: any) {
    return axios.get('/a/u/lessonFuzzy', {
        params: data,
    });
}
// 查询指定id的课时
function getClassHourId(id: number) {
    return axios.get(`/a/u/lesson/${id}`);
}
// 修改课时信息
function putClassHour(params: any) {
    return axios.put(`/a/u/lesson`, qs.stringify(params));
}
// 新增课时类型
function addClassHour(params: any) {
    return axios.post(`/a/u/lesson`, qs.stringify(params));
}
// 删除
function deleteClassHour(id: number) {
    return axios.delete(`/a/u/lesson/${id}`);
}
// 上下架
function ClassHourStatus(id: number) {
    return axios.put(`/a/u/lessonStatus/${id}`);
}
// 课程名称列表
function getCoursetName(data: any) {
    return axios.get('/a/u/courseName', {
        params: data,
    });
}
// 课程名称列表
function getLessonName(data: any) {
    return axios.get('/a/u/lessonName', {
        params: data,
    });
}
function getLessonId(data: any) {
    return axios.get('/a/u/lessonId', {
         params: data ,
         });
}
export {
    getClassHour,
    getClassHourFuzzy,
    getClassHourId,
    putClassHour,
    addClassHour,
    deleteClassHour,
    ClassHourStatus,
    getCoursetName,
    getLessonName,
    getLessonId,
};
