<template>
  <div>
    <v-header title="课程详情" class="is-fixed"></v-header>
    <div class="main">
      <mt-loadmore
        ref="loadmore"
        :top-method="loadTop"
        @top-status-change="topChange"
        :auto-fill="false"
      >
        <div slot="top">
          <span v-show="topState === 'loading'">
            刷新中……
          </span>
        </div>

        <div class="lesson">
          <div class="title">{{name}}</div>
          <div class="information">
            <img class="banner" :src="img" :onerror="errorGoodsImg">
            <div class="right-information">
              <div>
                <span class="w2">难度</span>
                <span>：</span>
                <span class="detail">{{difficulty}}级</span>
              </div>
              <div>
                <span class="w2">进度</span>
                <span>：</span>
                <span class="detail">{{finished}}/{{total}}</span>
              </div>
              <div>
                <span class="w3">资料数</span>
                <span>：</span>
                <span class="detail">{{files}}套</span>
              </div>
              <div>
                <span>学习人数</span>
                <span>：</span>
                <span class="detail">{{finishedCounts}}人</span>
              </div>
            </div>
          </div>
          <div class="intro">
            <p class="intro-title">课程简介：</p>
            <p class="intro-content">{{intro}}</p>
          </div>
        </div>
        <!-- 课时列表 -->
        <div class="lesson-list">
          <div
            class="lesson-item"
            v-for="(item, index) in lessonList"
            :key="index"
            @click="goLesson(item)"
          >
            <div class="lesson-content">
              <div class="lesson-title">
                {{item.lessonName}}
                <template v-if="lessonList.length<=3&&index>0||lessonList.length>3&&index>2">
                  <i class="icon-locked" v-if="item.isLocked"></i>
                  <!-- <i class="icon-unlocked" v-else></i> -->
                </template>
              </div>
              <div class="lesson-intro">{{item.lessonIntroduced}}</div>
            </div>
            <div
              class="lesson-status"
              :class="item.lessonStatus ==2?'finished':(item.lessonStatus==1?'studing':'unstudy')"
            >
              <span v-show="item.lessonStatus ==2">已完成</span>
              <span v-show="item.lessonStatus ==1">进行中</span>
              <span v-show="item.lessonStatus ==0">未学习</span>
            </div>
          </div>
        </div>
      </mt-loadmore>
    </div>
    <!-- footer -->
    <div class="footer">
      <div class="files flex-direction" v-if="files!==0">
        <i class="icon-files"></i>
        <span v-if="!orderId" @click="goBuyFiles">资料</span>
        <span v-else>已购买</span>
      </div>
      <div class="marked flex-direction">
        <template v-if="marked">
          <i class="icon-marked"></i>
          <span class="marked">已收藏</span>
        </template>
        <template v-else>
          <i class="icon-mark"></i>
          <span class="mark">未收藏</span>
        </template>
      </div>
      <div class="participation flex-direction">
        <i class="icon-participation"></i>
        <span>分享</span>
      </div>
      <div class="status" :class="{putdown:isPutaway}">
        <span class="study" v-if="finished ==0">开始学习</span>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Header.vue";
// import { getLessonDetail, favorites } from "@/http/lesson/lesson.js";
import { getClassHourFuzzy } from "@/http/curriculum/classhourManage.ts";
import { getCourseId,updateStudentState,checkStudentState,checkStudentlesson } from "@/http/curriculum/curriculumManage.ts";
export default {
  components: {
    "v-header": Header
  },
  data() {
    return {
      studentId: '',
      topState: "", //下拉加载状态
      cid: 0,
      total: 0, //课时总数
      name: "", //课程名称
      img: "", //封面地址
      difficulty: 0, //难度等级
      finishedCounts: 0, //完成人数
      finished: 0, //已完成课时数
      intro: "", //课程简介
      files: 0, //资料数
      marked: false, //是否被收藏
      lessonList: [], //课时列表
      isPutaway: false, //上下架状态
      errorGoodsImg: '',
    };
  },
  computed: {
    user: function() {
      return this.$store.state.user;
    }
  },
  created: function() {
    let user = JSON.parse(localStorage.getItem('user'));
    console.log(user);
    this.studentId = user.id;
    console.log("创建");
    console.log(this.$route);
    this.cid = this.$route.query.id; // 课程ID
    console.log(this.cid);
    let params = {
        studentId: this.studentId,
        courseId: this.cid,
    };
    updateStudentState(params)
    .then(res=>{
        console.log(res);
    })
    checkStudentState(params)
    .then(res=>{
        console.log(res)
    })
    this.getDetail();
  },
  methods: {
    //获取课程详情数据
    getDetail: function() {
      getCourseId(this.cid).then(res => {
        console.log(res);
        let data = res.data.data;
        this.name = data.courseName;
        this.img = data.coursePic;
        this.difficulty = data.courseLevel;
        this.finishedCounts = data.studyNumber;
        this.intro = data.courseIntroduction;
        let params = {
            courseName: this.name,
        };
       getClassHourFuzzy(params).then(res => {
        console.log(res)
        this.lessonList = res.data.data.records;
        let params = {
            studentId: this.studentId,
            lessonId: 52,
        }
        console.log(params)
        checkStudentlesson(params)
        .then(res=>{
            console.log(res)
        })
      });
      });
    },
    //下拉加载状态更改
    topChange: function(status) {
      this.topState = status;
    },
    //下拉刷新
    loadTop: function() {
      this.getDetail();
      this.allLoaded = false;
      this.$refs.loadmore.onTopLoaded();
    },
    //跳转课时详情
    goLesson: function(item) {
      this.$router.push({
        path: '/CurriculumHourList',
        query: {
          id: item.id,
        }
      });
    },
  }
};
</script>

<style scoped>
