import axios from '@/http/axios.ts';
// import axios from 'axios';
import qs from 'qs';
// 课程数据列表
function getTaskManage() {
    return axios.get('/a/u/allMission');
}
// 根据学生的id和对应的课时id, 找到对应的课时信息
function checkStudentlesson(data: any){
    return axios.get('/a/u/user/lesson');
}
// 根据学生的id和 课程id 找到对应的状态
function checkStudentState(data: any){
    return axios.get('/a/u/user/studentCourse', {
        params: data,
    });
}
// 根据学生的id和 课程id , 更新对应的状态;
function updateStudentState(params: any) {
    return axios.post('/a/u/user/studentCourse', qs.stringify(params));
}
// 查询科目id
function getSubjectId(data: any) {
    return axios.get(`/a/u/subjectId`, {
        params: data,
    });
}
// 查询课程的id
function getcourseId(data: any) {
    return axios.get(`/a/u/courseId`, {
        params: data,
    });
}
// 数据列表
function getCurriCulumm(data: any) {
    return axios.get(`/a/u/courseFuzzy`, {
        params: data,
    });
}
// 数据列表
function getCurriCulum(data: any) {
    return axios.get(`/a/u/courseFuzzy`, {
        params: data,
    });
}
// 查询指定id的课程
function getCourseId(id: number) {
    return axios.get(`/a/u/course/${id}`);
}
// 修改课程信息
function putCourse(params: any) {
    return axios.put(`/a/u/course`, qs.stringify(params));
}
// 新增课程
function addCourse(params: any) {
    return axios.post(`/a/u/course`, qs.stringify(params));
}
// 删除
function deleteCourse(id: number) {
    return axios.delete(`/a/u/course/${id}`);
}
// 上下架
function courseStatus(id: number) {
    return axios.put(`/a/u/courseStatus/${id}`);
}
// 上传图片
function uploadPhoto(params: any, config: any) {
    return axios.post('/a/u/course/img', params, config);
}
// 上传媒体
function uploadMedia(params: any, config: any) {
    return axios.post('/a/u/mission/img', params, config);
}
export {
    getTaskManage,
    checkStudentlesson,
    checkStudentState,
    updateStudentState,
    getCurriCulumm,
    getCurriCulum,
    getCourseId,
    putCourse,
    addCourse,
    deleteCourse,
    courseStatus,
    uploadPhoto,
    uploadMedia,
    getSubjectId,
    getcourseId,
};
